@font-face {
    font-family: 'SANGJUDajungdagam';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2112@1.0/SANGJUDajungdagam.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);

.notosanskr * { 
 font-family: 'Noto Sans KR', sans-serif;
}

/*컨테이너 및 로고*/

/* .signup_container {} */

.signup_background {
    position: absolute;
    z-index: -1;
    width: 1456px;
    height: 900px;
    left: 232px;
    top: 90px;
    background: #FFFFFF;
    border-radius: 16px;
}

.signup_all {
    margin-left: -45px;
}

.signup_header {
    display: flex;
}

.signup_logo {
    width: 200px;
    height: 100px;
    margin-left: 278px;
    margin-top: 116px;
}

.signup_logoContent {
    margin-left: 284px;
    margin-top: 310px;
    font-family: 'SANGJUDajungdagam';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 16px;
    letter-spacing: -0.005em;
    color: #43BAFE;
}

/*이름 입력*/

/* .signup_name {} */

.signup_name_text {
    width: 148px;
    height: 24px;
    margin-left: 550px;
    margin-top: 80px;
    font-family: 'Noto Sans KR', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 41px;
    color: #000000;
}

.signup_nameInput {
    box-sizing: border-box;
    width: 900px;
    height: 86px;
    margin-left: 549px;
    margin-top: 30px;
    background-color: rgba(255, 255, 255, 0.8);
    border: 4px solid rgba(0, 129, 222, 0.4);
    border-radius: 16px;
    font-size: 30px;
    padding-left: 10px;
    font-family: 'Noto Sans KR', sans-serif;
}

/*이메일 입력*/

/* .signup_email {} */

.signup_email_text {
    width: 148px;
    height: 24px;
    margin-left: 550px;
    margin-top: 24px;
    font-family: 'Noto Sans KR', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 41px;
    color: #000000;
}

.signup_emailInput {
    box-sizing: border-box;
    width: 900px;
    height: 86px;
    margin-left: 549px;
    margin-top: 30px;
    background-color: rgba(255, 255, 255, 0.8);
    border: 4px solid rgba(0, 129, 222, 0.4);
    border-radius: 16px;
    font-size: 30px;
    padding-left: 10px;
    padding-right: 10px;
    font-family: 'Noto Sans KR', sans-serif;
}

/*다음 버튼*/

/* .signup_next {} */

.signup_next_button {
    width: 184px;
    height: 48px;
    margin-left: 1266px;
    margin-top: 64px;
    background-color: #43b9feb7;
    color: #fff;
    border-radius: 32px;
    border: none;
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 35px;
    text-align: center;
}

.signup_next_button:hover {
    background-color: #2CB2FF;
}

/*서클*/

.checkCircle {
    display: flex;
    margin-top: 76px;
    margin-left: 924px;
}

.first_circle_checked {
    width: 32px;
    height: 16px;
    background-color: #2CB2FF;
    border-radius: 16px;
}

.second_circle {
    width: 16px;
    height: 16px;
    margin-left: 24px;
    background-color: #CAE8FF;
    border-radius: 16px;
}

.third_circle {
    width: 16px;
    height: 16px;
    margin-left: 24px;
    background-color: #CAE8FF;
    border-radius: 16px;
}

.fourth_circle {
    width: 16px;
    height: 16px;
    margin-left: 24px;
    background-color: #CAE8FF;
    border-radius: 16px;
}