/*packageList*/

.second_packageList {
    width: 646px;
    height: 764px;
    border: 1px solid rgba(0, 129, 222, 0.4);
    border-radius: 10px;
    float: left; /*★*/
    margin-left: 44px;
    margin-top: 24px;
    overflow-y: scroll;
    scrollbar-width: inherit;
}

.second_packageList::-webkit-scrollbar {
    background: #d5edff;
    border-radius: 10px;
    scrollbar-width: inherit;
}

.second_packageList::-webkit-scrollbar-thumb {
    background: #43BAFE;
    border-radius: 10px;
    background-clip: padding-box;
    border: 3px solid transparent;
    scrollbar-width: inherit;
}

.second_packageList::-webkit-scrollbar-track {
    background: #d5edff;
    border-radius: 10px;
    background-clip: padding-box;
}

.second_packageList::-webkit-scrollbar-thumb:hover {
    background: #43BAFE;
    background-clip: padding-box;
    border: 3px solid transparent;
}

.second_packageList::-webkit-scrollbar-thumb:active {
    background: #43BAFE;
    background-clip: padding-box;
    border: 3px solid transparent;
}

.second_packageList::-webkit-scrollbar-button {
    display: none;
}




