    font-weight: normal;
    font-style: normal;
}

@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);

.notosanskr * { 
 font-family: 'Noto Sans KR', sans-serif;
}

/*컨테이너 및 로고*/

.signup_third_background {
    position: absolute;
    z-index: -1;
    width: 1456px;
    height: 900px;
    left: 232px;
    top: 90px;
    background: #FFFFFF;
    border-radius: 16px;
}

.signup_third_all {
    margin-left: -45px;
}

.signup_third_header {
    display: flex;
}

.signup_third_logo {
    width: 200px;
    height: 100px;
    margin-left: 278px;
    margin-top: 116px;
}

.signup_third_logoContent {
    margin-left: 284px;
    margin-top: 310px;
    font-family: 'SANGJUDajungdagam';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 16px;
    letter-spacing: -0.005em;
    color: #43BAFE;
}

/*가이드 인증하기 입력*/

.guideCertify_text {
    width: 188px;
    height: 24px;
    margin-left: 550px;
    margin-top: 32px;
    font-family: 'Noto Sans KR', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 41px;
    color: #000000;
}

.guideCertify_plus {
    box-sizing: border-box;
    width: 900px;
    height: 352px;
    margin-left: 549px;
    margin-top: 30px;
    background-color: rgba(255, 255, 255, 0.8);
    border: 4px solid rgba(0, 129, 222, 0.4);
    border-radius: 16px;
    font-size: 30px;
    padding-left: 10px;
    font-family: 'Noto Sans KR', sans-serif;
}

.guideCertify_plus_Icon {
    width: 40px;
    height: 40px;
    margin-left: 430px;
    margin-top: 156px;
    /* opacity: 0.5; */
}

/*다음 버튼*/

.signup_third_next_button {
    width: 184px;
    height: 48px;
    margin-left: 1266px;
    margin-top: 64px;
    background-color: #43b9feb7;
    color: #fff;
    border-radius: 32px;
    border: none;
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 35px;
    text-align: center;
}

.signup_third_next_button:hover {
    background-color: #2CB2FF;
}

/*서클*/

