    background-color: #CAE8FF;
    border-radius: 16px;
}

.third_circle_checked {
    width: 32px;
    height: 16px;
    margin-left: 24px;
    background-color: #2CB2FF;
    border-radius: 16px;
}

.fourth_circle {
    width: 16px;
